@charset "utf-8";

#clock {
  /* width: 340px; */
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid transparent;


  /* min-width: 140px; */

}

#clock .clockFont {
  /* font-size: 80px; */
  font-weight: 700;
  margin: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

#clock span {
  /* font-size: 24px; */
  font-weight: 400;
  display: inline-block;
  margin-left: 20px;
  margin-bottom: 0;
}
