
#modal{
    display: none;

    position: fixed;  
    /* 부모에 transform있으면 이거 픽스드가 제대로 적용이 안되네 */
    left: 0 ;
    top: 0;
   
    
    z-index: 112;
    width: 100vw;
    height: 100vh;
    background-color: gray;
      /*부모에 opacity가 들어갔을 경우 rgba로 자식으 따로 설정할 수 있음  */
    background-color: rgba(128, 128, 128, 0.8);

    justify-content: center;
    align-items: center;
    

}
.memo{
    margin-top: -80px;
    border: 1px solid #000;
    width: 400px;
    height: 400px;
    background-color: rgba(255,244,79,1);
  
    /* opacity: 1 !important; */
    

    z-index: 115;

    display:flex;
    align-items: center;
    justify-content: center;
    position: relative;
    flex-flow: row wrap;

}
.memoText{
    margin: 20px;
    word-break: break-all;
   
}
.memo input[type="checkbox"]{
    display: none;
}

.memo label[for="x"]{
    display: block;
    font-size: 32px;


    position: absolute;
    top: 8px;
    right: 8px;
}